/* eslint-disable @next/next/no-img-element */
import React from "react";
import { motion } from "framer-motion";

const fadeIn = (direction = "up", delay = 0) => {
  return {
    hidden: {
      opacity: 0,
      y: direction === "up" ? 50 : direction === "down" ? -50 : 0,
      x: direction === "left" ? 50 : direction === "right" ? -50 : 0,
    },
    visible: {
      opacity: 1,
      y: 0,
      x: 0,
      transition: {
        duration: 0.8,
        delay,
        ease: "easeOut",
      },
    },
  };
};

const AboutPage = () => {
  return (
    <div>
      <motion.h1 className="title-h1" initial="hidden" animate="visible" variants={fadeIn("up", 0.2)}>
        О бренде
      </motion.h1>
      {/* =========================== */}
      <div className="flex flex-col gap-[70px] mt-[30px]">
        <motion.div
          className="flex justify-center gap-[40px] items-center"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
        >
          <motion.div className="max-w-[550px]" variants={fadeIn("left", 0.3)}>
            <img
              src="https://lesyanebo.com/local/templates/nebo_2022/images/about-lesya.jpg"
              alt=""
              className="rounded-lg shadow-lg"
            />
          </motion.div>
          <motion.div className="max-w-[450px] w-full" variants={fadeIn("right", 0.5)}>
            <h2 className="text-[30px] mb-[50px]">Lesyanebo был создан Олесей Шиповской в 2014 году.</h2>
            <p className="text-[30px]">
              Бренд Lesyanebo — это идеальный баланс маскулинности и женственности, подходящий как для повседневной
              жизни, так и для особых случаев.
            </p>
          </motion.div>
        </motion.div>

        <motion.div
          className="flex justify-center gap-[40px] items-center"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
        >
          <motion.div className="max-w-[450px] text-right w-full" variants={fadeIn("left", 0.3)}>
            <h2 className="text-[30px] mb-[50px]">Lesyanebo был создан Олесей Шиповской в 2014 году.</h2>
            <p className="text-[30px]">
              Бренд Lesyanebo — это идеальный баланс маскулинности и женственности, подходящий как для повседневной
              жизни, так и для особых случаев.
            </p>
          </motion.div>
          <motion.div className="max-w-[550px]" variants={fadeIn("right", 0.5)}>
            <img
              src="https://lesyanebo.com/local/templates/nebo_2022/images/about-lesya.jpg"
              alt=""
              className="rounded-lg shadow-lg"
            />
          </motion.div>
        </motion.div>
      </div>
      {/* ======================== */}
    </div>
  );
};

export default AboutPage;
